import { Send } from "@material-ui/icons";
import { useState } from "react";
import styled from "styled-components";
import { mobile } from "../responsive";

const Container = styled.div`
  height: 60vh;
  background-color: #fcf5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;
const Title = styled.h1`
  font-size: 70px;
  margin-bottom: 20px;
`;

const Desc = styled.div`
  font-size: 24px;
  font-weight: 300;
  margin-bottom: 20px;
  ${mobile({ textAlign: "center" })}

`;

const InputContainer = styled.div`
  width: 50%;
  height: 40px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  border: 1px solid lightgray;
  ${mobile({ width: "80%" })}
`;

const Input = styled.input`
  border: none;
  flex: 8;
  padding-left: 20px;
`;

const Button = styled.button`
  flex: 1;
  border: none;
  background-color: #F5EA5A;
  color: white;
`;

const Newsletter = () => {
  const [text, setText] = useState('')

  const handleButtonClick = async () => {
    try {
      const response = await fetch('http://localhost:8000/api/nltext', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text }),
      });
      const data = await response.json();
      console.log(data.message);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <Container>
      <Title>Newsletter</Title>
      <Desc>To register a new account and get timely updates, please enter your Email here</Desc>
      <InputContainer>
        <Input value={text} onChange={(e) => setText(e.target.value)} placeholder="Your email" />
        <Button onClick={handleButtonClick}>
          <Send />
        </Button>
      </InputContainer>
    </Container>
  );
};

export default Newsletter;
